/* src/components/Projects/Projects.css */
.projects-box {
  max-width: 800px;
  margin: 0 auto;
  border: 2px solid #ffcc00;
  border-radius: 10px;
  overflow: hidden;
  background-color: #333;
  padding: 20px;
  box-shadow: 0px 0px 10px 0px rgba(255, 204, 0, 0.75); 
  margin-bottom: 100px;

}
.projects-p{
  text-align: center;
  font-size: 20px;
}

.projects-heading {
  text-align: center;
  font-size: 36px;
  margin-bottom: 20px;
  color:#ffcc00;
}


.projects-navbar {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 20px;
}

.project-nav-item {
  cursor: pointer;
  padding: 10px 20px;
  color: #fff;
  margin: 0 10px; 
  border: 2px solid #ffcc00; 
  border-radius: 8px; 
}

.project-nav-item.active {
  background-color: #ffcc00; 
  color: #333;
}

.project-info {
  text-align: center;
  color: #fff;
}

.project-info h2 {
  font-size: 32px;
  margin-bottom: 15px;
}

.project-info h3 {
  font-size: 24px;
  margin-bottom: 10px;
}

.project-info p {
  font-size: 16px;
}

.project-card {
  background-color: #444;
  padding: 20px;
  border: 2px solid #ffcc00;
  border-radius: 8px;
  color: #fff;
}
